import React from 'react'
import { connect } from 'react-redux'

const Shout = ({ login }) => (
  <div>
    { console.log(login) }
    <label htmlFor="inputPassword5">Password</label>
    <input type="password" id="inputPassword5" className="form-control" aria-describedby="passwordHelpBlock" />
    <p id="passwordHelpBlock" className="form-text text-muted"> Your password must be 8-20 characters long, contain letters and numbers, and must not contain spaces, special characters, or emoji.</p>
  </div>
)

Shout.propTypes = {
  login: React.PropTypes.object
}

export default connect(({ login }) => ({login}), {})(Shout)



