const initialState = {
  auth: false
}

export const login = (state = initialState, action) => {
  switch (action.type) {
    case 'LOGIN':
      state = Object.assign(state, { auth: true })
      break
    default:
      state = Object.assign({}, state)
      break
  }
  return state
}



